class App extends Component {
  state = {
    activeItem: "About",
    content: initialContent,
  };

  componentDidMount() {
    document.addEventListener("scroll", () => {
      if (window.scrollY > 3 && window.scrollY < 500) {
        this.setState({ activeItem: "About" });
      }
      if (window.scrollY > 500 && window.screenY < 1000) {
        this.setState({ activeItem: "Experience" });
      }
      if (window.scrollY > 1000) {
        this.setState({ activeItem: "Projects" });
      }
    });
  }

  handleItemClick = (name) => {
    scrollToComponent(this[name], {
      offset: -75,
      align: "top",
      duration: 700,
    });
  };

  handleTwitterClick = (t) => {
    window.location = "t";
  };

  render() {
    const buttonStyle = { margin: "10px" };
    return (
      <div className="wrapper">
        <Nav
          links={this.state.content.about}
          activeItem={this.state.activeItem}
          handleItemClick={this.handleItemClick}
        />

        <ContentWrapper
          ref={(el) => {
            this.About = el;
          }}
        >
          <About summary={this.state.content.about} />
          {/* <NewSkill  skills={this.state.content.about.skills} />*/}
        </ContentWrapper>

        <ContentWrapper
          ref={(el) => {
            this.Experience = el;
          }}
        >
          <div id="sec">
            {/*<Skills id='skills' skills={this.state.content.about.skills}/>*/}
            <Experience id="exp" work={this.state.content.experience} />
          </div>
        </ContentWrapper>

        <ContentWrapper
          ref={(el) => {
            this.Projects = el;
          }}
        >
          <Projects projects={this.state.content.personalProjects} />
        </ContentWrapper>
      </div>
    );
  }
}

export default App;
