import React from "react";
import { Header, Icon, Image, Button, Label } from "semantic-ui-react";
import "./App.css";

const HeaderContent = ({ display }) => (
  <Header.Subheader className="info">{display}</Header.Subheader>
);

const Skills = ({ display }) => (
  <Header.Subheader className="info">
    {display.map((skill) => (
      <Label id="skill-label" basic color="black" size="large" key={skill}>
        {skill}
      </Label>
    ))}
  </Header.Subheader>
);

const buttonStyle = { margin: "10px" };

const About = ({ summary }) => (
  <div className="content">
    <Header
      as="h2"
      icon
      textAlign="center"
      inverted
      style={{ fontSize: "2.71rem" }}
    >
      <Image bordered src={summary.image} style={{ width: "120px" }} circular />
      <Header.Content>{summary.name}</Header.Content>
      <HeaderContent display={summary.role} style={{ "font-size": "large" }} />
      <Skills display={summary.skills} />
      <p
        style={{ display: "inline" }}
        display={
          <a style={{ color: "white" }} href={`mailto:${summary.email}`}>
            {" "}
          </a>
        }
      >
        {summary.email}
      </p>
      <br />
      <p display={summary.phone} style={{ display: "inline" }}>
        {summary.phone}
      </p>
    </Header>
  </div>
);

export default About;
