import React from "react";
import { Card, List, Label, Header, Icon } from "semantic-ui-react";
import "./App.css";

const WorkCard = ({ company, details, appreciations, duration, role }) => {
  let description = details.map((list, index) => (
    <List.Item key={`description${index}`}>
      <List.Icon name="code" />
      <List.Content>{list}</List.Content>
    </List.Item>
  ));

  if (appreciations && appreciations.length > 0) {
    description.push(
      <List.Item key={"appreciations.index"}>
        {/*<List.Icon name='thumbs up' />*/}
        <i className="trophy icon"></i>
        <List.Content>{appreciations}</List.Content>
      </List.Item>
    );
  }

  return (
    <Card>
      <Card.Content></Card.Content>
    </Card>
  );
};

const Experience = ({ work }) => {
  let description = work[0].details.map((list, index) => (
    <List.Item key={`description${index}`}>
      <i className="pencil alternate icon"></i>
      <List.Content>{list}</List.Content>
    </List.Item>
  ));

  if (work[0].appreciations && work[0].appreciations.length > 0) {
    description.push(
      <List.Item key={"appreciations.index"}>
        <i className="trophy icon"></i>
        <List.Content>{work[0].appreciations}</List.Content>
      </List.Item>
    );
  }
  return (
    <div className="content">
      <Header as="h2" inverted textAlign="left">
        {/*<Icon name='code' textAlign='center' />*/}
        <i className="wrench icon"></i>
        <Header.Content>Work Experience</Header.Content>
      </Header>
      <Card.Group stackable itemsPerRow={2}>
        {work.map((workCard, index) => (
          <Card id="inexp" key={index}>
            <Card.Content>
              {/*<Label  ribbon='right' color='orange'>{workCard.duration}</Label>*/}

              <Card.Header
                className="company"
                style={{ display: "inline-block" }}
              >
                {workCard.company}
              </Card.Header>
              <Label
                as="a"
                color="grey"
                style={{ float: "right", fontSize: "1rem" }}
              >
                {workCard.duration}
              </Label>
              <Card.Meta className="role">{workCard.role}</Card.Meta>
              <Card.Description className="description">
                <List>{description}</List>
              </Card.Description>
            </Card.Content>
          </Card>
        ))}
      </Card.Group>
    </div>
  );
};

export default Experience;
