import React from "react";
import { Card, Header, Icon, Button } from "semantic-ui-react";
import "./App.css";

const Projects = ({ projects }) => {
  return (
    <div className="content">
      <Header as="h2" inverted textAlign="left">
        <i className="folder open outline icon"></i>
        <Header.Content>Projects</Header.Content>
      </Header>
      <Card.Group stackable itemsPerRow={2}>
        {projects.map((project, index) => (
          <Card className="projects" key={index}>
            <Card.Content>
              <Card.Header>{project.name}</Card.Header>
              <Card.Meta>{project.tech}</Card.Meta>
              <Card.Description>{project.details}</Card.Description>
            </Card.Content>
            <Card.Content extra>
              <div className="ui two buttons">
                <Button
                  href={project.github}
                  target="_blank"
                  basic
                  color="black"
                >
                  <Icon name="github" /> Github
                </Button>
              </div>
            </Card.Content>
          </Card>
        ))}
      </Card.Group>
    </div>
  );
};

export default Projects;
